import cookie from 'react-cookies'

// cookie
export const loginUser = () => {
  return cookie.load('__csrf')
}

// cookie
export const onLogin = (cookieStr) => {
  document.cookie = cookieStr
}

// cookie
export const logout = () => {
  cookie.save('MUSIC_U', '', {})
  cookie.remove('MUSIC_U')
  cookie.remove('__csrf')
}