/**
 * 
 * @param {string} content textarea content
 * @returns string[]
 */
export function formatLyrics(content) {
  //  ,  \n,  \n  ()
  return content
    .trim()
    .replace(/(\r\n|\r)/g, '\n')
    .replace(/(\n)\1+/g, '\n\n')
    .split('\n')
}

/**
 * 
 *
 *  jsmediatags.js https://github.com/aadsm/jsmediatags
 *
 * @param {*} musicfile
 * @returns promise({code,msg,data})
 */
export async function getMusicInfo(musicfile) {
  return new Promise((resolve, reject) => {
    window.jsmediatags.read(musicfile, {
      onSuccess: function (tag) {
        //url
        let picture
        if (tag.tags.picture) {
          let imageData = tag.tags.picture.data
          let base64String = ''
          for (let i = 0; i < imageData.length; i++) {
            base64String += String.fromCharCode(imageData[i])
          }
          picture =
            'data:' +
            tag.tags.picture.format +
            ';base64,' +
            window.btoa(base64String)
        }
        resolve({
          code: 200,
          msg: '',
          data: {
            artist: tag.tags.artist || '',
            name: tag.tags.title || '',
            album: tag.tags.album || '',
            picUrl: picture || '',
          },
        })
      },
      onError: reject,
    })
  })
}
